form {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  width: 100%;
  max-width: 506px;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 6px #0000001a;
}

label {
  color: #333;
  margin-bottom: 6px;
  font-weight: bold;
}

input[type="text"] {
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 15px;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s;
}

input[type="text"]:focus {
  border-color: #666;
  outline: none;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #444;
  border: none;
  border-radius: 8px;
  margin-top: 10px;
  padding: 10px;
  font-size: 1rem;
  transition: background-color .3s;
}

input[type="submit"]:hover {
  background-color: #222;
}

